i Stomatologicznej zosta powoany wraz z Instytutem Stomatologii AM w Lublinie w dniu 1 padziernika 1974 roku. W 1985 roku przy Zakadzie powstaa Pracownia Zaburze Czynnociowych Narzdu ucia. Pracownia ta bya na pocztku jedyn tego typu wyodrbnion placwk naukow w kraju, zajmujc si leczeniem chorych z blami twarzy i gowy na tle dysfunkcji ukadu stomatognatycznego. W momencie powoania Zakad Protetyki Stomatologicznej zatrudnia 5 pracownikw naukowo-dydaktycznych. Obecnie w Zakadzie Protetyki Stomatologicznej jest zatrudnionych 23 nauczycieli akademickich (5 adiunktw i 18 asystentw). Pracownicy Katedry i Zakadu Protetyki Stomatologicznej aktywnie dziaaj w Polskim Towarzystwie Stomatologicznym. Kilku pracownikw Zakadu wchodzi w skad Zarzdu Oddziau PTS w Lublinie. Lekarze pracujcy w Zakadzie Protetyki Stomatologicznej prowadz szkolenia przeddyplomowe i podyplomowe. Realizowany jest program szkolenia przeddyplomowego z zakresu protetyki stomatologicznej oraz materiaoznawstwa stomatologicznego dla studentw I, III, IV, V roku kierunku lekarsko-dentystycznego (wiczenia przedkliniczne, wiczenia kliniczne, wykady).\n      ")])]):_vm._e()]),_vm._v(" "),_c('div',{staticClass:"team"},[_c('img',{attrs:{"src":require("./assets/images/zespol-radej600x600.jpg")}}),_vm._v(" "),_c('h2',[_vm._v("DR KATARZYNA RADEJ")]),_vm._v(" "),_c('h3',[_vm._v("SPECJALNO PEDODOCJA (STOMATOLOGIA  DZIECICA, ZACHOWAWCZA)")]),_vm._v(" "),_c('p',[_vm._v("\n      Absolwent Uniwersytetu Medycznego w Lublinie. Obecnie wykadowca w Katedrze i Zakadzie Protetyki Stomatologicznej Uniwersytetu Medycznego w   Lublinie. W 2014 uzyska stopie specjalisty w zakresie protetyki stomatologicznej. Autor publikacji w czasopismach branowych. Uczestnik licznych krajowych i zagranicznych kursw i konferencji naukowych.\n    ")]),_vm._v(" "),_c('p',{staticClass:"team-link",on:{"click":_vm.showTeam2}},[_c('strong',[_vm._v("Wicej")]),_vm._v(" "),_c('i',{staticClass:"fa fa-chevron-circle-down",attrs:{"aria-hidden":"true"}})]),_vm._v(" "