<template>
  <div id="team">
    <h1>Nasz zesp</h1>


    <div class="team">
      <img src="./assets/images/zespol-sykut600x600.jpg">
      <h2>DR N. MED JANUSZ SYKUT</h2>
      <h3>SPECJALNO  PROTETYKA, CHIRURGIA, IMPLANTOLOGIA</h3>
      <p>
        Absolwent Uniwersytetu Medycznego w Lublinie. Autor publikacji w czasopismach branowych. Uczestnik licznych krajowych i zagranicznych kursw oraz konferencji naukowych. Janusz Sykut zajmuje si rwnie zabiegami Medycyny Estetycznej. Obecnie adiunkt w Katedrze i Zakadzie Protetyki Stomatologicznej Uniwersytetu Medycznego w Lublinie.
      </p>
      <p class="team-link" @click="showTeam1"><strong>Wicej</strong> <i class="fa fa-chevron-circle-down" aria-hidden="true"></i></p>
      <div v-if="team1">
        <p>
          W latach 2001-2002 Janusz Sykut by asystentem w Zakadzie Stomatologii Zachowawczej UM w Lublinie. 2001-2007 asystent w Poradni Chirurgii Stomatologicznej Wojewdzkiej Przychodni Stomatologicznej w Lublinie. 2002-2006 rezydent w trakcie specjalizacji w Katedrze i Zakadzie Protetyki Stomatologicznej z Pracowni Implantologii Stomatologicznej i Pracowni Zaburze Czynnociowych Narzdu ucia Uniwersytetu Medycznego w Lublinie. W 2004 uzyska stopie doktora nauk medycznych. W 2006 uzyska tytu specjalisty w dziedzinie Protetyki Stomatologicznej. W 2014 roku ukoczy czteroletni Implant Continuum Education Program w Collage of Dentistry New York University. W 2015 uzyska certyfikat Umiejtnoci w Dziedzinie Implantologii Stomatologicznej EDI OSIS.
        </p>
        <p>
          Prywatne zainteresowania: sporty wodne i zimowe, motoryzacja, film.
        </p>
        <p>
          Janusz Sykut jest pracownikiem naukowym <a href="http://www.zakladprotetyki.umlub.pl/pracownicy/" target="_blank" rel="noopener noreferrer" tabindex="0">Uniwersytetu Medycznego w Lublinie </a>
        </p>
        <p>
          Wicej: Zakad Protetyki Stomatologicznej zosta powoany wraz z Instytutem Stomatologii AM w Lublinie w dniu 1 padziernika 1974 roku. W 1985 roku przy Zakadzie powstaa Pracownia Zaburze Czynnociowych Narzdu ucia. Pracownia ta bya na pocztku jedyn tego typu wyodrbnion placwk naukow w kraju, zajmujc si leczeniem chorych z blami twarzy i gowy na tle dysfunkcji ukadu stomatognatycznego. W momencie powoania Zakad Protetyki Stomatologicznej zatrudnia 5 pracownikw naukowo-dydaktycznych. Obecnie w Zakadzie Protetyki Stomatologicznej jest zatrudnionych 23 nauczycieli akademickich (5 adiunktw i 18 asystentw). Pracownicy Katedry i Zakadu Protetyki Stomatologicznej aktywnie dziaaj w Polskim Towarzystwie Stomatologicznym. Kilku pracownikw Zakadu wchodzi w skad Zarzdu Oddziau PTS w Lublinie. Lekarze pracujcy w Zakadzie Protetyki Stomatologicznej prowadz szkolenia przeddyplomowe i podyplomowe. Realizowany jest program szkolenia przeddyplomowego z zakresu protetyki stomatologicznej oraz materiaoznawstwa stomatologicznego dla studentw I, III, IV, V roku kierunku lekarsko-dentystycznego (wiczenia przedkliniczne, wiczenia kliniczne, wykady).
        </p>
      </div>
    </div>

    <div class="team">
      <img src="./assets/images/zespol-radej600x600.jpg">
      <h2>DR KATARZYNA RADEJ</h2>
      <h3>SPECJALNO PEDODOCJA (STOMATOLOGIA  DZIECICA, ZACHOWAWCZA)</h3>
      <p>
        Absolwent Uniwersytetu Medycznego w Lublinie. Obecnie wykadowca w Katedrze i Zakadzie Protetyki Stomatologicznej Uniwersytetu Medycznego w   Lublinie. W 2014 uzyska stopie specjalisty w zakresie protetyki stomatologicznej. Autor publikacji w czasopismach branowych. Uczestnik licznych krajowych i zagranicznych kursw i konferencji naukowych.
      </p>
      <p class="team-link" @click="showTeam2"><strong>Wicej</strong> <i class="fa fa-chevron-circle-down" aria-hidden="true"></i></p>
      <div v-if="team2">
        <p>
          W latach 2004-2017 pracownik student studiw doktoranckich oraz asystent w Katedrze i Zakadzie Protetyki Stomatologicznej z Pracowni Implantologii Stomatologicznej i Pracowni Zaburze Czynnociowych Narzdu ucia Uniwersytetu Medycznego w Lublinie.<br>
          Zajmuje si rwnie zabiegami Medycyny Estetycznej.
        </p>
        <p>
          Prywatne zainteresowania: sporty wodne i zimowe, motoryzacja, film.
        </p>
      </div>
    </div>
    <div class="team">
      <img src="./assets/images/zespol-kopec600x600.jpg">
      <h2>DR PRZEMYSAW KOPE</h2>
      <h3>SPECJALNO ENDODONCJA</h3>
      <p>
        Podpora naszego gabinetu od 2010 r. Ukoczya Technologi ywnoci na Uniwersytecie Przyrodniczym, a nastpnie odbya wiele kursw i szkole wewntrznych oraz zewntrznych (eby tylko wymieni: asysta, sterylizacja, dezynfekcja, ergonomia pracy, recepcja). Iza wsppracuje ze wszystkimi lekarzami Prodentica Stomatologia. Jej gwne zadania to: asysta przy zabiegach, sterylizacja, obsuga recepcji, koordynacja pracy gabinetu.
      </p>
    </div>
    <h1>Kompleksowe leczenie & profilaktyka</h1>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      team1: false,
      team2: false

    }
  },
  methods: {
    showTeam1() {
      this.team1 = true;
    },
    showTeam2() {
      this.team2 = true;
    }

  }
}
</script>

<style>
#team {
  display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;      /* TWEENER - IE 10 */
  display: -webkit-flex;     /* NEW - Chrome */
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

#team h1, #team h2 {
  width: 100%;
}

.team {
  width: 33.3%;
  padding: 5px;
}

.team img {
  width: 100%;
}

.team-link {
  cursor: pointer;
}

@media screen and (max-width: 599px) {

  .team {
    width: 100%;
  }

}

@media screen and (min-width: 600px) and (max-width: 999px) {


}
</style>



