<template>
  <div id="aboutus">
    <h1>O nas</h1>

    <div class="aboutus">
      <p>
        Dziaajc od dwudziestu lat w stomatologii i wychodzc naprzeciw oczekiwaniom Pacjentw, ktrym pomoglimy i pomagamy, stworzylimy miejsce szczeglne w Lublinie  grupow praktyk stomatologiczn z zespoem nastawionym na interdyscyplinarn obsug pacjentw.
      </p>
      <p>
        Praca jest dla nas pasj, dlatego zdobyt wiedz przenosimy wprost do gabinetw, by suy Wam  Pacjentom, by szybko i rzetelnie rozwizywa zoone problemy z zakresu stomatologii zachowawczej, estetycznej, ortodoncji, pedodoncji, protetyki, implantologii i periodontologii.
      </p>
      <p>
        Praktyka grupowa w Prodentice, w ktrej opiek nad Pacjentem sprawuje kilku lekarzy  kady w swoim wyspecjalizowanym zakresie  pozwala na wprowadzenie wszystkich  specjalnoci stomatologii i moliwo traktowania leczenia uzbienia kompleksowo  od dzieci do dorosych.
      </p>
      <p>
      Oddajemy Pacjentw pod opiek lekarzy i pielgniarek. W rejestracji pomoe i udzieli informacji zesp recepcjonistek.
      </p>
    </div>

    <div class="sidebar">
      <img src="./assets/images/onas600x400.jpg">
    </div>

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<style>
#aboutus {
  display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;      /* TWEENER - IE 10 */
  display: -webkit-flex;     /* NEW - Chrome */
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

#aboutus h1 {
  width: 100%;
}
.aboutus {
  width: 60%;
  padding: 5px;
}

.sidebar {
  width: 40%;
  padding: 5px;
}

.sidebar img {
  width: 100%;
}

@media screen and (max-width: 599px) {
  .aboutus, .sidebar {
    width: 100%;
    padding: 5px;
  }

}

@media screen and (min-width: 600px) and (max-width: 999px) {


}
</style>



